import React, { useState } from 'react';
import { IconButton, Badge, Popover, Paper, Typography, List, ListItem, ListItemText, ListItemAvatar, Avatar, Button, Snackbar, Alert } from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import DeleteIcon from '@mui/icons-material/Delete';
import { useCart } from '../context/CartContext';
import emailjs from 'emailjs-com';

const Cart = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const { cart, removeFromCart, resetCart } = useCart();
    const total = calculateTotalPrice(cart);
    const [openAlert, setOpenAlert] = useState(false);
    const handleCloseAlert = () => {
        setOpenAlert(false);
    }

    const handleCartClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleCartClose = () => {
        setAnchorEl(null);
    };

    const handleRemoveItem = (item) => {
        // Logica para remover un item del carrito
        removeFromCart(item)
    };

    const handleCheckout = () => {
        let str = "";
        cart.map((item) => {
            str += `${item.product.name}, cantidad: ${item.quantity ? item.quantity : 1}, precio unitario: $${item.product.price} \n`;
        });

        let template = {
            to_email: 'agustinimarcelo@gmail.com',
            subject: 'Nuevo pedido',
            str: str
        }

        emailjs.send(
            'service_vd4fhry',
            'template_gpz2eas',
            template,
            '1kJkddVlofxdGh33o').then((result) => {
                if (result.text === 'OK') {
                    setOpenAlert(true)
                    resetCart();
                    handleCartClose();
                }
            }, (error) => {
                console.log(error.text);
            });

    };

    function calculateTotalPrice(cart) {
        let total = 0;
        for (const item of cart) {
            total += item.product.price * item.quantity;
        }
        return total;
    }

    return (
        <div>
            <Badge badgeContent={cart.length} color="secondary">
                <IconButton color="inherit" aria-label="Shopping Cart" onClick={handleCartClick}>
                    <ShoppingCartIcon />
                </IconButton>
            </Badge>
            <>
                <Popover
                    open={open}
                    anchorEl={anchorEl}
                    onClose={handleCartClose}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                >
                    <Paper sx={{ padding: 2 }}>
                        <Typography variant="h6" gutterBottom>
                            Mi Carrito de compras
                        </Typography>
                        {cart.length > 0 ? (
                            <>
                                <List>
                                    {cart.map((item, index) => (
                                        <ListItem key={index}>
                                            <ListItemAvatar>
                                                <Avatar alt="Product Image" src={item.product.image} />
                                            </ListItemAvatar>
                                            <ListItemText primary={`${item.product.name} (Cant.: ${item.quantity})`} secondary={item.product.price ? `$${item.product.price.toFixed(2)}` : ''} />
                                            <IconButton
                                                color="error"
                                                aria-label="Delete"
                                                onClick={() => handleRemoveItem(item)}
                                            >
                                                <DeleteIcon />
                                            </IconButton>
                                        </ListItem>
                                    ))}
                                </List>
                                <Typography variant="h6" sx={{ textAlign: 'right' }}>
                                    Total Carrito: ${total.toFixed(2)}
                                </Typography>
                                <div style={{ display: 'flex', justifyContent: 'center' }}>
                                    <Button variant="contained" color="primary" onClick={handleCheckout}>
                                        Checkout
                                    </Button>
                                </div>
                            </>
                        ) : (
                            <Typography variant="body2">Su carrito est vaco.</Typography>
                        )}
                    </Paper>

                </Popover>
                <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'center' }} open={openAlert} autoHideDuration={6000} onClose={handleCloseAlert}>
                    <Alert onClose={handleCloseAlert} severity="success" sx={{ width: '100%' }}>
                        Pedido Enviado!
                    </Alert>
                </Snackbar>
            </>
        </div>
    )
}

export default Cart;
